.conteneur {
  font-family: sans-serif;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: grid;
  grid-template-columns: repeat(1, 200px);
  grid-gap: 10px;
  padding-top: 10px;
  padding-bottom: 30px;
}
.conteneur .box {
  width: 100%;
  height: 100%;
}
.conteneur .box h2 {
  display: block;
  text-align: center;
  color: rgb(3, 3, 3);
  font-style: bold;
}
.conteneur .box .chart {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 30px;
  line-height: 140px;
  height: 140px;
  color: rgb(10, 10, 10);
}
.conteneur .box canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
